.Map > span {
    background-color: transparent;
    border: orange solid 3px;
    box-sizing: border-box;
    height: 50px;
    width: 50px;
    position: absolute;
    transform: scale(0);
    transition: transform 80ms ease-in;
}

.Map > span.visible {
    transform: scale(1);
}

.Map > span > ul {
    position: relative;
    right: -55px;
    padding: 0;
    width: 60px;
    top: 0px;
    margin: 0;
    /* background-color: white; */
    text-align: center;
}

.Map ul li {
    list-style: none;
}

.Map li button {
    width: 100%;
    cursor: pointer;
    font-weight: bold;
}
.Map li button:hover {
    background-color: rgb(107, 107, 189);
}

.Map img {
    height: 1600px;
    width: auto;
    margin: 0 auto;
    display: block;
}

.Map {
    background-color: darkgray;
    min-height: 100vh;
}

.Map p {
    font-size: 3rem;
    font-weight: bold;
    margin: 0 auto;
    display: block;
}
